.my-btn {
  text-transform: uppercase;
  border: none;
  display: inline-block;
  vertical-align: middle;
  outline: none;
  font-size: 14px;
  line-height: 20px;
  font-weight: bold;
  color: #ffffff;
  padding: 15px 15px;
  border-radius: 25px;
  cursor: pointer;
  text-align: center;
  background: #F0F0F0;
  letter-spacing: 0.02em;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15); }
  .my-btn:hover {
    background: #D96F18;
    box-shadow: none; }
  .my-btn--block {
    width: 100%;
    display: block; }
  .my-btn--light {
    background: #ffffff;
    color: #000; }
    .my-btn--light:hover {
      background: #EA7D24;
      color: #ffffff; }
  .my-btn--fix {
    width: 260px; }
  .my-btn--small {
    font-size: 14px;
    width: 175px;
    padding: 10px 15px; }

