.wrap-txt-block__box {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px; }

.wrap-txt-block__col50 {
  width: 50%;
  padding: 0px 20px; }

.wrap-txt-block > * {
  margin-bottom: 20px; }
  .wrap-txt-block > *:last-child {
    margin-bottom: 0; }

.wrap-txt-block p {
  margin-bottom: 15px; }

.wrap-txt-block ul {
  padding-left: 18px;
  margin-bottom: 15px; }
  .wrap-txt-block ul li {
    margin-bottom: 15px; }

.wrap-txt-block ol {
  counter-reset: myCounter;
  margin-bottom: 15px; }
  .wrap-txt-block ol li {
    list-style: none;
    margin-bottom: 10px; }
  .wrap-txt-block ol li:before {
    counter-increment: myCounter;
    content: counter(myCounter);
    color: black;
    border-radius: 50%;
    display: inline-block;
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    line-height: 19px;
    width: 18px;
    height: 18px;
    margin-right: 7px; }

.wrap-txt-block a {
  text-decoration: underline; }

.wrap-txt-block table {
  margin-bottom: 15px;
  width: 100%; }

.wrap-txt-block iframe {
  width: 550px;
  height: 390px;
  display: block;
  margin: 0 auto 20px; }

.wrap-txt-block blockquote {
  border: 1px solid #F0F0F0;
  display: block;
  width: 100%;
  padding: 30px 55px;
  position: relative;
  font-weight: 500; }
  .wrap-txt-block blockquote:before {
    content: "";
    position: absolute;
    background: url("../../img/blockquote-icon.png") no-repeat #fff center top;
    width: 45px;
    height: 29px;
    top: -15px;
    left: 55px; }

